@use '../abstracts/variables' as v;

// Reset button styles.
.reset-search-icon {
  color: white;
  font-size: 2.5rem;
  left: 15px;
  position: relative;
  top: 10px;

  &:hover {
    color: lightgray;
    cursor: pointer;
  }
}

// Searchbar styles.
.searchbar-wrapper {
  display: flex;
  margin: auto;
  min-width: 100px;
  position: relative;
  width: clamp(300px, 80vw, 800px);
}

.searchbar {
  background-color: white;
  border-radius: 35px 0px 0px 35px;
  border: none;
  font-family: v.$roboto;
  font-size: 1.3rem;
  height: 60px;
  outline: 0;
  padding: 2px 23px 2px 30px;
  width: 100%;
}

.search-btn {
  border-radius: 0px 35px 35px 0px;
  border: none;
  color: white;
  cursor: pointer;
  font-size: 1.5rem;
  width: 18%;
}

.clear-icon {
  cursor: pointer;
  font-size: 1.2rem;
  position: absolute;
  right: clamp(50px, 20%, 150px);
  top: 20px;
  width: 10px;

  &:hover {
    color: gray;
  }
}

.search-color-green {
  background-color: lighten($color: v.$brba-green, $amount: 15);

  &:hover {
    background-color: lighten($color: v.$brba-green, $amount: 10);
  }
}

.search-color-red {
  background-color: darken($color: v.$bcs-red, $amount: 10);
}

.search-color-yellow {
  background-color: hsl(56, 80, 50);
}
